@import url(https://fonts.googleapis.com/css2?family=Advent+Pro:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Barlow+Condensed&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

form{
    text-align: left;
}

form label, label input, label select{
    display: block;
    font-family: 'Barlow Condensed', sans-serif;
    font-size: 16px;
    text-align: left;
}

label input, label select{
    width: 100%;
    padding: 15px;
    margin-top: 10px;
}
form label{
    margin-top: 20px;
}

form button{
    border-radius: 5px;
    background: #FFFAA0;
    border: none;
    box-shadow: 0px 3px 10px 0px rgba(0,0,0,0.35);
    padding: 15px 30px;
    margin-top: 15px;
    font-family: 'Barlow Condensed', sans-serif;
    font-size: 16px;
}
.current-issues{
    max-height: 360px;
    overflow: hidden;
    overflow-y: scroll;
    padding-right: 30px;
}

.current-issues p{
    text-align: left;
    color: #F3F0F0;
    margin: 10px 0;
}

.current-issues p span{
    display: inline-block;
    margin-left: 20px;
}

.current-issues p span, .current-issues .description-text{
    color: black;
    opacity: 50%;
}

.current-issues .indv-issue{
    position: relative;
}

.current-issues .close-issue{
    position: absolute;
    top: 0;
    right: 0;
    color: #F3F0F0;
    opacity: 70%;
    margin-top: 0;
}

.current-issues hr{
    border: none;
    background:#F3F0F0;
    height: 1px;
    margin-top: 20px;
    margin-bottom: 20px;
}

.App {
  text-align: center;
}

button{
  cursor: pointer;
}

body{
  background:  #F8EEE9;
}

h1{
  font-size: 65px;
  font-family: 'Advent Pro', sans-serif;
  color: #50C878;
}

.card-container{
  background-color:	#D2B48C;
  max-width: 36%;
  margin: auto;
  box-shadow: 0px 3px 10px 0px rgba(0,0,0,0.35);
  border-radius: 3px;
  color: white;
  padding: 40px 50px 30px;
  font-family: 'Barlow Condensed', sans-serif;
  font-size: 20px;
}

.current-issues::-webkit-scrollbar-track
{
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
  background-color: white;
}

.current-issues::-webkit-scrollbar
{
  width: 6px;
  background-color: white;
}

.current-issues::-webkit-scrollbar-thumb
{
  background-color: #8CAAD2;
}

.view-button{
  border-radius: 5px;
  background: white;
  color: #50C878;
  border: none;
  box-shadow: 0px 3px 10px 0px rgba(0,0,0,0.35);
  padding: 15px 30px;
  font-family: 'Barlow Condensed', sans-serif;
  font-size: 16px;
}

.view-button:hover{
  letter-spacing: 1px;
  transition: all .5s ease;
}

.active-button{
  border-radius: 5px;
  background: #50C878;
  color:white;
  border: none;
  box-shadow: 0px 3px 10px 0px rgba(0,0,0,0.35);
  padding: 15px 30px;
  font-family: 'Barlow Condensed', sans-serif;
  font-size: 16px;
}

.active-button:hover{
  letter-spacing: 1px;
  transition: all .5s ease;
}

.view-selection{
  display: flex;
  justify-content: space-between;
  max-width: 25%;
  margin: auto;
  margin-top: 40px;
}

